<template>
  <div :class="{'hidden':hidden}" class="pagination-container">
    <el-pagination
      :background="background"
      :current-page.sync="currentPage"
      :page-size.sync="pageSize"
      :layout="layout"
      :page-sizes="pageSizes"
      :total="total"
      v-bind="$attrs"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
    />
  </div>
</template>

<script>
import { Utils } from "@/utils";
export default {
  name: "Pagination",
  props: {
    total: {
      required: true,
      type: Number,
      default: 200
    },
    page: {
      type: Number,
      default: 1
    },
    limit: {
      type: Number,
      default: 20
    },
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 30, 50];
      }
    },
    layout: {
      type: String,
      default: "total, sizes, prev, pager, next, jumper"
    },
    background: {
      type: Boolean,
      default: true
    },
    autoScroll: {
      type: Boolean,
      default: true
    },
    hidden: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    currentPage: {
      get() {
        return this.page;
      },
      set(val) {
        this.$emit("update:page", val);
      }
    },
    pageSize: {
      get() {
        return this.limit;
      },
      set(val) {
        this.$emit("update:limit", val);
      }
    }
  },
  methods: {
    handleSizeChange(val) {
      this.$emit("pagination", { page: this.currentPage, limit: val });
      if (this.autoScroll) {
        Utils.scrollTo(0, 800);
      }
    },
    handleCurrentChange(val) {
      console.log(val)
      this.$emit("pagination", { page: val, limit: this.pageSize });
      if (this.autoScroll) {
        Utils.scrollTo(0, 800);
      }
    }
  }
};
</script>

<style lang="less" scoped>
.pagination-container {
  background: #fff;
  padding: 32px 16px;
  text-align: center;
  
}

.pagination-container.hidden {
  display: none;
}
</style>